	module.exports = {"data":{"site":{"siteMetadata":{"title":"Hablando con Máquinas","author":"Jonay Godoy"}},"allMarkdownRemark":{"edges":[{"node":{"excerpt":"Hoy voy a hablar del comando de git “—no-ff” sobretodo porque cuando vi este comando\nme pregunte ¿Por que alguien iba a querer evitar el...","frontmatter":{"title":"No Fast Forward","path":"/no-fast-forward/","date":"03 December, 2017"}}},{"node":{"excerpt":"Este es el primer año que se hace el HackForGood BigDay por lo que creo que\nes una buena idea contar mi experiencia para que otras personas...","frontmatter":{"title":"Hack For Good Big Day","path":"/hack-for-good-big-day/","date":"29 September, 2017"}}},{"node":{"excerpt":"Buenas, yo se nada sobre la mejor o la peor o manera de hacer un curriculum.Pero si sé una cosa odio invertir tiempo en mantener información...","frontmatter":{"title":"Convierte tu perfil Linkedin en un curriculum","path":"/convierte-tu-perfil-de-Linkedin-en-tu-cv/","date":"12 September, 2017"}}},{"node":{"excerpt":"Screeps Se describe como un juego para programadores. Programa a tu colonia de creeps para que se expanda y conquiste nuevas zonas. Un MMO...","frontmatter":{"title":"Empezamos con los juegos educativos.Screeps y Codecombat","path":"/empezamos-con-los-juegos-educativos/","date":"09 September, 2017"}}},{"node":{"excerpt":"Los métodos estáticos en general, hace nuestro día a día un poco más cómodo pero si tienes una mínima duda de si deberías utilizarlo lo...","frontmatter":{"title":"Como testear métodos estáticos","path":"/como-testear-metodos-estaticos/","date":"01 July, 2017"}}},{"node":{"excerpt":"Últimamente me encontrado con muchos programadores que no ven el switch como un mal olor.\nEs más hace alrededor de un año yo tampoco lo...","frontmatter":{"title":"Kata Gilded Rose Switch","path":"/kata-gilded-rose-switch/","date":"30 May, 2017"}}},{"node":{"excerpt":"Me parecía adecuado escribir como uno de los primeros artículos\nblog. Todo el trayecto que tiene mi blog porque aunque me dé algo\nde...","frontmatter":{"title":"La Historia De Mi Blog","path":"/la-historia-de-mi-blog/","date":"30 May, 2017"}}},{"node":{"excerpt":"Buenas, hoy quería hablar sobre mi primera experiencia ludum. Aunque antes os voy a poner en contexto para todo aquel que desconozca el...","frontmatter":{"title":"Ludum Dare GC 35, Mi primera Ludum","path":"/ludum-dare-gc-35-mi-primera-ludum/","date":"29 December, 2016"}}}]},"markdownRemark":{"id":"/home/workspace/repositories/problemDependecy/gatsby-blog/src/pages/2017-09-12-como-hacer-un-cv-con-vuestro-perfil-de-linkedin/index.md absPath of file >>> MarkdownRemark","html":"<p>Buenas, yo se nada sobre la mejor o la peor o manera de hacer un curriculum.Pero si sé una cosa odio invertir tiempo en mantener información por duplicada.\nMuchas personas me han recomendado con razón tener un perfil de Linkedin, sin embargo me siento como si estuviera trabajando el doble al tener que mantener actualizada la información\nprofesional tanto en mi perfil de Linkedin como en mi cv.Quizás simplemente sea un poco maniático con la información, me podéis llamar informático para abreviar.\nAhora enserio esta es la típica cosa que unas personas le resultará super util y a otras personas será inútil, yo la verdad simplemente disfruto actualizando los\ndatos y viendo como el curriculum se actualiza solo. </p>\n<p>Si sois tan maniáticos como yo podéis seguir los siguientes pasos:</p>\n<h3>- Primer Paso, <strong>extraer los datos de Linkedin.</strong></h3>\n<p>Realmente es fácil de hacer pero se me hizo un poco dura porque no sabia como hacerlo exactamente.\nEncontré dos opciones, intentar utilizar la Api o pedir a Linkedin que te envié tus datos. Sorprendentemente, el segundo resulta más fácil, cómodo\ny es algo casi inmediato para lo que queremos aunque no descarto hacer un intento con la Api en el futuro.\nEl siguiente enlace os redirige directamente donde podéis descargar vuestra información.\nRecomiendo elegir la opción <strong>“Archivo rápido únicamente”</strong>.Para terminar ir a vuestro correo seguid los pasos y tendréis un zip con vuestro datos.\n<br>\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/87387a1e25254275ba6d76f354bb0d82-d9228.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 563px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 57.06666666666666%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"antes\"\n        title=\"\"\n        src=\"/static/87387a1e25254275ba6d76f354bb0d82-b411a.jpg\"\n        srcset=\"/static/87387a1e25254275ba6d76f354bb0d82-d39d0.jpg 148w,\n/static/87387a1e25254275ba6d76f354bb0d82-c383c.jpg 295w,\n/static/87387a1e25254275ba6d76f354bb0d82-b411a.jpg 590w,\n/static/87387a1e25254275ba6d76f354bb0d82-d9228.jpg 750w\"\n        sizes=\"(max-width: 563px) 100vw, 563px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/929af200e6a2789427e53ca00d2d627a-4c92f.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 29.74316487158244%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"despues\"\n        title=\"\"\n        src=\"/static/929af200e6a2789427e53ca00d2d627a-f8fb9.jpg\"\n        srcset=\"/static/929af200e6a2789427e53ca00d2d627a-e8976.jpg 148w,\n/static/929af200e6a2789427e53ca00d2d627a-63df2.jpg 295w,\n/static/929af200e6a2789427e53ca00d2d627a-f8fb9.jpg 590w,\n/static/929af200e6a2789427e53ca00d2d627a-85e3d.jpg 885w,\n/static/929af200e6a2789427e53ca00d2d627a-d1924.jpg 1180w,\n/static/929af200e6a2789427e53ca00d2d627a-4c92f.jpg 1207w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n        </p>\n<h3>- Segundo paso, <strong>pasar de csv a json</strong>.</h3>\n<p>Problema cuando extraemos los datos, nos damos cuenta que nuestros datos están en .csv y lo peor en archivos distintos.\nPodéis programar vosotros mismo un conversor que coja vuestro zip seleccione vuestro archivos y lo convierta a un .json o el formato que queráis yo uso un\njson o podéis usar este <a href=\"https://jmperezperez.com/linkedin-to-json-resume/\" target=\"_blank\">linkedin-to-json-resume</a>. Me he basado en está página para crear este articulo.\nPero hay un pequeño problema, tal como está hoy, este conversor hay cierto datos que no nos extrae. Lo que hago es buscar el .csv con los datos que me faltan\nconvertirlos con un <a href=\"http://www.csvjson.com/csv2json\" target=\"_blank\">conversor online</a> y unirlo a la parte final de json que ya me tenía. ¿Por que no he programado un<br>\nque se adapte a mis necesidades? Simplemente, yo actualizo mi cv entre 3 meses a 1 año y todo este el proceso un vez sabes hacerlo tardas como 20 min en actualizar\ntu .json. Quizás un día lo programe por amor al arte pero a día de hoy no creo me compense automatizar el proceso.\n</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>\"basics\": {\n    \"name\": \"Jonay Eliezer Godoy Reyes\",\n    \"label\": \"Developer\",\n    \"phone\": \"\",\n    \"website\": \"\",\n    \"summary\": \"Apasionado por el aprendizaje\",\n    \"location\": {\n    \"address\": \"\",\n        \"postalCode\": \"\",\n        \"city\": \"\",\n        \"countryCode\": \"\",\n        \"region\": \"\"\n    },\n    \"profiles\": []\n},\n\"work\": [\n    {\n        \"company\": \"Codesai\",\n        \"position\": \"Alumno Practicas\",\n        \"website\": \"\",\n        \"startDate\": \"2017-03-01\",\n        \"summary\": \"-Aprender. A través de practicas como mob-programming, libros, cursos e implementando\\nfuncionalidades en algunos proyectos siempre buscando hacerlo de le mejor manera posible.\\nAsi como metodologias como integración continua y TDD\\n-Las tecnologías que utilizamos fueron javascript, react-redux y springboot(java). Asi como\\nherramientas complementarias para testear Jest, mocha, junit, selenium.\",\n        \"highlights\": [],\n        \"endDate\": \"2017-06-01\"\n    },.....</code></pre>\n      </div>\n<h3>- Tercer paso, <strong>ya tengo mi .json es hora de darle formato.</strong></h3>\n<p>Yo de momento el .json lo meto dentro de un archivo .js y lo importo a un html porque me es más cómodo por supuesto\nustedes tenéis libertad para usar otro método.\nAhora solo queda darle formato al html a vuestro gusto. Recomiendo que también busquéis como convertirlo a .pdf, yo por ahora lo hago de la manera más rápida\nimprimiendo con el navegador, también existen páginas y plugins que os permite seleccionar una parte de html para imprimirlo, o incluso existe la posibilidad de\ndefinir en un css como se ve una página web a la hora de imprimir.\n<br>\n<br>\n<br>\nPara terminar os pongo un enlace a <a href=\"../conoceme\" target=\"_blank\">mi cv en html</a> y a mi <a href=\"https://www.linkedin.com/in/jonay-eliezer-godoy-reyes-34a099111\" target=\"_blank\">Linkedin</a>\npor queréis ver un ejemplo.</p>\n<p><a href=\"https://jmperezperez.com/linkedin-to-json-resume/\" target=\"_blank\">referencia: https://jmperezperez.com/linkedin-to-json-resume/</a></p>","frontmatter":{"title":"Convierte tu perfil Linkedin en un curriculum","path":"/convierte-tu-perfil-de-Linkedin-en-tu-cv/","date":"September 12, 2017","image_article":{"relativePath":"2017-09-12-como-hacer-un-cv-con-vuestro-perfil-de-linkedin/cover.jpg"}}}},"pathContext":{"path":"/convierte-tu-perfil-de-Linkedin-en-tu-cv/"}}

